<template>
  <div class="container">
    <br>
    <b-form>
    <h2 id="section_instance_config" style="padding: 1rem;">Cybereason Server Configuration</h2>
    <b-card no-body>
      <b-tabs card>
          <b-tab title="Server Configuration" active>
            <b-card-text>
              <tenant-fields :config="configSettings" @event-save="save_settings" @event-add="add_tenant"></tenant-fields>
            </b-card-text>
          </b-tab>
          <b-tab title="Proxy Setup">
            <b-card-text>
              <proxy-fields :proxy="configSettings.proxy_https" @event-save-proxy="save_settings"></proxy-fields>
            </b-card-text>
          </b-tab>
        </b-tabs>
    </b-card>
    </b-form>
  </div>
</template>

<script>
import ConfigForm from './ConfigForm.vue'
import ProxyForm from './ProxyForm.vue'
import CRApi from '../api/cr-api'

const api = () => new CRApi() // Do this to compute the baseUrl on each call

export default {
  components: {
    'tenant-fields': ConfigForm,
    'proxy-fields': ProxyForm
  },
  data () {
    return {
      messages: [],
      configSettings: {}
    }
  },
  methods: {
    get_configSettings () {
      api().get('/get_config')
        .then(response => {
          if (response.data.status === 'success') {
            this.configSettings = response.data.configSettings
          }
        })
        .catch(err => {
          console.error(err)
        })
    },
    save_settings () {
      var postSettings = JSON.parse(JSON.stringify(this.configSettings))
      api().post('/save_configs', postSettings)
        .then(response => {
          this.messages = response.data.messages
          this.$fire({
            title: 'Success',
            text: this.messages,
            type: 'success',
            timer: 10000
          }).then(r => {
            this.$router.go(this.$router.currentRoute)
          })
        })
        .catch(err => {
          console.error(err)
        })
    },
    add_tenant (tenant) {
      var postSettings = JSON.parse(JSON.stringify(this.configSettings))
      postSettings.host_info.push(tenant)
      api().post('/add_tenant', postSettings)
        .then(response => {
          this.messages = response.data.messages
          if (this.messages[0] === 'Configuration have been saved') {
            this.$fire({
              title: 'Success',
              text: this.messages,
              type: 'success',
              timer: 10000
            }).then(r => {
              this.$router.go(this.$router.currentRoute)
            })
          } else {
            this.$fire({
              title: 'Alert',
              text: this.messages,
              type: 'info',
              timer: 10000
            }).then(r => {
              this.$router.go(this.$router.currentRoute)
            })
          }
        })
        .catch(err => {
          console.error(err)
        })
    }
  },
  created () {
    this.$forceUpdate()
    this.get_configSettings()
    this.$forceUpdate()
  }
}
</script>

