<script>
import CRApi from '../api/cr-api'
const api = () => new CRApi()

export default {
  props: ['config'],
  data () {
    return {
      user_input: { poll_suspicions: false, poll_action_logs: false, auth_type: 'basic' },
      log_source: this.get_log_source(),
      selected_log_source: null,
      selected: '',
      terms: false
    }
  },
  computed: {
    is_disabled: function () {
      return !this.terms
    }
  },
  methods: {
    validate (userInput) {
      if (userInput.server && userInput.port && userInput.username && userInput.password && userInput.malop_escalation_interval && userInput.malop_back_days && this.selected_log_source) {
        if (userInput.malop_escalation_interval > 0 && userInput.malop_back_days > 0) {
          this.add_tenant(userInput)
        } else {
          this.$fire({
            title: 'Warning',
            text: 'Please provide correct value for the mandatory fields',
            type: 'warning',
            timer: 10000
          }).then(r => {
            console.log(r.value)
          })
        }
      } else {
        this.$fire({
          title: 'Warning',
          text: 'Please provide correct value for the mandatory fields',
          type: 'warning',
          timer: 10000
        }).then(r => {
          console.log(r.value)
        })
      }
    },
    unsaved_config () {
      this.terms = true
    },
    add_tenant (userInput) {
      this.$emit('event-add', userInput)
    },
    new_tenant () {
      this.$forceUpdate()
      this.user_input = { poll_suspicions: false, poll_action_logs: false, auth_type: 'basic' }
      this.selected_log_source = null
      this.selected = ''
      this.get_log_source()
      this.$forceUpdate()
    },
    confirm (index) {
      this.$confirm('Are you sure you want to delete this tenant?').then(() => {
        this.delete_tenant(index)
      })
    },
    delete_tenant (index) {
      this.config.host_info.splice(index, 1)
      this.$emit('event-save')
    },
    save_tenant (index) {
      if (this.config.host_info[index].malop_back_days > 0 && this.config.host_info[index].malop_escalation_interval > 0) {
        this.$emit('event-save')
      } else {
        this.$fire({
          title: 'Warning',
          text: 'Please provide correct value for the mandatory fields',
          type: 'warning',
          timer: 10000
        }).then(r => {
          console.log(r.value)
        })
      }
    },
    get_log_source () {
      api().get('/get_log_source')
        .then(response => {
          if (response.data.status === 'success') {
            this.log_source = response.data.log_sources
          }
        })
        .catch(err => {
          console.error(err)
        })
    },
    add_log_source (event) {
      this.selected_log_source = event.target.options[event.target.options.selectedIndex].text
      this.user_input.log_source = this.selected_log_source
    }
  }
}
</script>
