<template>
  <div class="container">
    <div class="form-row">
      <div class="container col-md-12">
        <b-row>
          <b-col class="col-md-2">
            <div class="custom-control custom-checkbox" style="margin-top:35px;">
              <input
                type="checkbox"
                class="custom-control-input"
                :id="proxy.idProxy"
                :name="proxy.idProxy"
                v-model="proxy.chkProxy"
                v-on:click="reload_ip_checkbox"
              />
              <label class="custom-control-label" :for="proxy.idProxy">Use Proxy</label>
            </div>
          </b-col>
          <b-col>
            <fieldset :disabled="!proxy.chkProxy">
              <label class="col-form-label col-form-label-sm" for="proxy_ip">Proxy IP</label>
              <input
                type="text"
                class="form-control form-control-sm"
                id="proxy_ip"
                name="proxy_ip"
                v-model="proxy.proxy_ip"
                required
              />
            </fieldset>
          </b-col>
          <b-col>
            <fieldset :disabled="!proxy.chkProxy">
              <label class="col-form-label col-form-label-sm" for="proxy_port">Proxy Port</label>
              <input
                type="number"
                class="form-control form-control-sm"
                id="proxy_port"
                name="proxy_port"
                v-model="proxy.proxy_port"
                required
              />
            </fieldset>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="col-md-2">
              <div class="custom-control custom-checkbox" style="margin-top:15px;">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  :id="proxy.idAuth"
                  :name="proxy.idAuth"
                  v-model="proxy.chkProxyAuth"
                  v-on:click="reload_user_checkbox"
                />
                <label class="custom-control-label" :for="proxy.idAuth">Proxy Authentication</label>
                <input type="hidden" id="hdnchkProxyAuth" />
              </div>
          </b-col>
          <b-col>
            <fieldset :disabled="!proxy.chkProxyAuth">
            <label class="col-form-label col-form-label-sm" for="proxy_user">Proxy User</label>
            <input
              type="text"
              value="Proxy User"
              class="form-control form-control-sm"
              id="proxy_user"
              name="proxy_user"
              v-model="proxy.proxy_user"
              required
            />
            </fieldset>
          </b-col>
          <b-col>
            <fieldset :disabled="!proxy.chkProxyAuth">
            <label class="col-form-label col-form-label-sm" for="proxy_pass">Proxy Password</label>
            <input
              type="password"
              value
              class="form-control form-control-sm"
              id="proxy_pass"
              name="proxy_pass"
              v-model="proxy.proxy_pass"
              required
            />
            </fieldset>
          </b-col>
        </b-row>
        <b-row class="text-center" style="margin-top: 2rem;">
          <b-col>
              <fieldset>
              <input
                type="button"
                class="btn btn-primary"
                id="btn-save"
                value="Save Proxy Settings"
                v-on:click="validate"
              />
              </fieldset>
          </b-col>
        </b-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'proxy',
  props: {
    proxy: {
      type: Object
    }
  },
  methods: {
    validate () {
      if (this.proxy.chkProxy) {
        if (this.proxy.proxy_ip && this.proxy.proxy_port) {
          if (this.proxy.chkProxyAuth) {
            if (this.proxy.proxy_user && this.proxy.proxy_pass) {
              console.log('Saving server details with authentication')
              this.save_proxy()
            } else {
              this.$fire({
                title: 'Warning',
                text: 'Please provide the authentication details',
                type: 'warning',
                timer: 10000
              }).then(r => {
                console.log(r.value)
              })
            }
          } else {
            console.log('Saving server details')
            this.proxy.proxy_user = ''
            this.proxy.proxy_pass = ''
            this.save_proxy()
          }
        } else {
          this.$fire({
            title: 'Warning',
            text: 'Please provide the server details',
            type: 'warning',
            timer: 10000
          }).then(r => {
            console.log(r.value)
          })
        }
      } else {
        console.log('No proxy settings provided')
        this.save_proxy()
      }
    },
    save_proxy () {
      this.$emit('event-save-proxy')
    },
    reload_ip_checkbox () {
      this.proxy.proxy_ip = ''
      this.proxy.proxy_port = ''
    },
    reload_user_checkbox () {
      this.proxy.proxy_user = ''
      this.proxy.proxy_pass = ''
    }
  }
}
</script>
