import store from '../store'

export default class CRApi {
  constructor () {
    // Get the Base URL from the Vuex store
    this.baseUrl = store.state.api.baseUrl
    this.csrfToken = store.state.api.csrfToken
  }

  get (url) {
    const options = {
      method: 'GET',
      headers: {
        'X-CSRF-Token': this.csrfToken
      }
    }
    return this._fetch(this.baseUrl + url, options)
  }

  post (url, data) {
    const options = {
      method: 'POST',
      headers: {
        'X-CSRF-Token': this.csrfToken,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    }
    return this._fetch(this.baseUrl + url, options)
  }

  _fetch (url, options) {
    var status = null
    return fetch(url, options)
      .then(response => {
        if (response.type === 'error') {
          return Promise.reject(new Error('Network error'))
        } else {
          status = response.status
          return response.text()
        }
      })
      .then(result => {
        const formattedResult = {
          status,
          data: JSON.parse(result || '{}')
        }
        if (status >= 200 && status <= 299) {
          return Promise.resolve(formattedResult)
        } else {
          return Promise.reject(formattedResult)
        }
      })
  }
}
