import Vue from 'vue'
import Vuex from 'vuex'
import api from './modules/api'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

const store = new Vuex.Store({
  modules: {
    api
  },
  strict: debug
})

store.dispatch('api/setBaseUrl', window.QRADAR_APP_URL || 'http://localhost:5000')
store.dispatch('api/setCsrfToken', window.CSRF_TOKEN || 'csrf')

export default store
